.tab{
    width: 343px;
    height: 48px;
    padding-left: 16px;
    padding-right: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #D0C9D6;
    background: #FFFFFF;
    box-shadow: inset 0px -1px 0px rgba(208, 201, 214, 0.4);
}
.tab__box{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}
.tab__text{
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
    line-height: 18px;
    font-size: 13px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 16px;
}
.tab__text_active{
    color: #1A051D;
}
.tab__text_default{
    color: #D0C9D6;
}
.tab__line{
    position: relative;
    display: block;
    height: 3px;
    width: 100%;
    border-radius: 200px 200px 0px 0px;
    bottom: 0px;
    left: 0;
}
.tab__line_active{
    background: #6979F8;
}